      sortArr(arr, str) {
    var _arr = [],
        _t = [],
        // 临时的变量
        _tmp;
 
    // 按照特定的参数将数组排序将具有相同值得排在一起
    arr = arr.sort(function(a, b) {
        var s = a[str],
            t = b[str];
 
        return s < t ? -1 : 1;
    });
 
    if ( arr.length ){
        _tmp = arr[0][str];
    }
    // console.log( arr );
    // 将相同类别的对象添加到统一个数组
    for (var i in arr) {
        console.log( _tmp);
        if ( arr[i][str] === _tmp ){
            console.log(_tmp)
            _t.push( arr[i] );
        } else {
            _tmp = arr[i][str];
            _arr.push( _t );
            _t = [arr[i]];
        }
    }
    // 将最后的内容推出新数组
    _arr.push( _t );
    return _arr;
},
     importExcel(evt) {//读取方法
      this.$message({
                  message: '上传成功',
                  type: 'success'
                });
      this.buyList=[];          
     let that=this;
	var wb; //读取完成的数据
	var rABS = false; //是否将文件读取为二进制字符串
	var isCSV;
	if(!evt) {
		return;
	}
	var f = evt.raw;
	var reader = new FileReader();
 
	var pet = new Promise(function(resolve, reject) {
		reader.onload = function(e) {
			var data = e.target.result;
			var XLSX = require('xlsx');
			wb = null;
			if(isCSV) {
				data = new Uint8Array(data);
				let f = that.isUTF8(data);
				//console.log("是CSV文件,编码" + (f ? "是" : "不是") + "UTF-8");
				if(f) {
					data = e.target.result;
				} else {
					var str = cptable.utils.decode(936, data);
					wb = XLSX.read(str, {
						type: "string"
					});
				}
			} else {
				//console.log("不是CSV文件");
			}
			if(!wb) {
				wb = rABS || isCSV ? XLSX.read(btoa(that.fixdata(data)), {
					type: 'base64'
				}) : XLSX.read(data, {
					type: 'binary'
				});
			}
			//console.log(wb);
			//wb.SheetNames[0]是获取Sheets中第一个Sheet的名字
			//wb.Sheets[Sheet名]获取第一个Sheet的数据
			//console.log(JSON.parse(JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])).replace(/\s+/g,"")));
      var result = JSON.parse(JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])).replace(/\s+/g,"").replace(/'/g,''));
      var array=[];
      if(that.orignal=='淘宝天猫'){
         for(var i=0;i<result.length;i++){
            array.push(
              {
                orignal:that.orignal,
                orderNum:result[i].订单编号.toString(),
                userName:result[i].收货人姓名,
                mobile:result[i].联系手机.toString(),
                address:result[i].收货地址,
                excelGoodsName:result[i].宝贝标题,
                buyAccount:result[i].买家会员名,
                singleOrderDtoList:[]
              }
            )
         }
      }else if(that.orignal=='京东'){
         for(var i=0;i<result.length;i++){
            array.push(
              {
                orignal:that.orignal,
                orderNum:result[i].订单号.toString(),
                userName:result[i].客户姓名,
                mobile:result[i].联系电话.toString(),
                address:result[i].客户地址,
                excelGoodsName:result[i].商品名称,
                buyAccount:result[i].下单帐号,
                singleOrderDtoList:[]
              }
            )
         }
      }else if(that.orignal=='拼多多'){
         for(var i=0;i<result.length;i++){
            array.push(
              {
                orignal:that.orignal,
                orderNum:result[i].订单号.toString(),
                userName:result[i].收货人,
                mobile:result[i].手机.toString(),
                address:result[i].省+result[i].市+result[i].区+result[i].街道,
                excelGoodsName:result[i].商品,
                singleOrderDtoList:[]
              }
            )
         }
      }

      that.tableData=array;
			resolve(result);
		}
		isCSV = f.name.split(".").reverse()[0] == "csv"; //判断是否是 CSV
		if(rABS || isCSV) {
			reader.readAsArrayBuffer(f);
		} else {
			reader.readAsBinaryString(f);
		}
		evt.value = "";
 
	}).catch(error => console.log(error));
	console.log(pet);
	return pet;
},
fixdata(data) { //文件流转BinaryString
	var o = "",
		l = 0,
		w = 10240;
	iconv.skipDecodeWarning = true;
	let str = iconv.encode(o, 'utf-8');
	for(; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)));
	o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
	return o;
},
isUTF8(bytes) { //非中文格式CSV文件转换UTF-8方法
	var i = 0;
	while(i < bytes.length) {
		if(( // ASCII
				bytes[i] == 0x09 ||
				bytes[i] == 0x0A ||
				bytes[i] == 0x0D ||
				(0x20 <= bytes[i] && bytes[i] <= 0x7E)
			)) {
			i += 1;
			continue;
		}
		if(( // non-overlong 2-byte
				(0xC2 <= bytes[i] && bytes[i] <= 0xDF) &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0xBF)
			)) {
			i += 2;
			continue;
		}
		if(( // excluding overlongs
				bytes[i] == 0xE0 &&
				(0xA0 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF)
			) ||
			( // straight 3-byte
				((0xE1 <= bytes[i] && bytes[i] <= 0xEC) ||
					bytes[i] == 0xEE ||
					bytes[i] == 0xEF) &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF)
			) ||
			( // excluding surrogates
				bytes[i] == 0xED &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0x9F) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF)
			)
		) {
			i += 3;
			continue;
		}
		if(( // planes 1-3
				bytes[i] == 0xF0 &&
				(0x90 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF) &&
				(0x80 <= bytes[i + 3] && bytes[i + 3] <= 0xBF)
			) ||
			( // planes 4-15
				(0xF1 <= bytes[i] && bytes[i] <= 0xF3) &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF) &&
				(0x80 <= bytes[i + 3] && bytes[i + 3] <= 0xBF)
			) ||
			( // plane 16
				bytes[i] == 0xF4 &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0x8F) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF) &&
				(0x80 <= bytes[i + 3] && bytes[i + 3] <= 0xBF)
			)
		) {
			i += 4;
			continue;
		}
		return false;
	}
	return true;
},
handleSelectionChange(val) {
    //this.trueList = val;
     this.buyList=this.sortArr(val,'excelGoodsName');

        console.log(this.buyList)
    this.falseList = this.tableData.filter(x => !val.find(y => y.orderNum === x.orderNum)); 
},

  }
};
