<script>
import foot from "./public/foot.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      priceForm:'',
    };
  },
  mounted(){
    this.getPrice() 
  },
  methods: {
    getPrice() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"expressPrice/my",
          "/giftmall/expressPrice/my",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             that.priceForm=response.data.data;
          } 
        });
    },
  }
};
</script>

