<template>
  <div>
    <top></top>
    <div class="box">
        <div class="loginCenter">
             <div class="loginBg">
                <div class="loginTitle">欢迎来到礼品淘</div>
                <div class="loginDesc">我们提供赠品采购、发货、一站式服务。</div>
                <div class="inp1"><el-input prefix-icon="el-icon-mobile" placeholder="请输入手机号" v-model="account"> </el-input></div>
                
                <div class="inp1"><el-input prefix-icon="el-icon-lock" show-password placeholder="请输入密码" v-model="password"> </el-input></div>
                <div class="mar" ><el-link type="danger" @click="wangji=true">忘记密码</el-link></div>
                <el-row :gutter="20" style="margin-top:15px;">
                     <el-col :span="12"><el-button @click="login" class="submitBt" type="primary">登录</el-button></el-col>
                     <el-col :span="12"><el-button @click="register" class="submitBt" type="info">注册</el-button></el-col>
                </el-row>  
             </div>  
        </div>  
    </div>
    <div style="position:fixed;left:0;bottom:0;width:100%;"><foot></foot></div>
      <el-dialog
        title="找回密码"
        :visible.sync="wangji"
        width="30%">
        <div class="inp1"><el-input prefix-icon="el-icon-mobile" placeholder="请输入手机号" v-model="account"> </el-input></div>
                <div class="inp1">
                    <el-input prefix-icon="el-icon-lock" placeholder="请输入验证码" v-model="smsCode"> </el-input>
                    <el-button @click="getCode" :disabled="isGet" class="getCode" type="text">{{codeText}}</el-button>
                </div>
                <div class="inp1"><el-input prefix-icon="el-icon-lock" show-password placeholder="请设置密码" v-model="password"> </el-input></div>

        <span slot="footer" class="dialog-footer">
          <el-button @click="wangji = false">取 消</el-button>
          <el-button type="primary" @click="wangji = false;forget()">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import foot from './public/foot.vue'
import top from './public/top.vue'
import {hex_md5} from "../utils/md5";
export default {
  components: {
    foot,
    top
  },
  data() {
    return {
      account:'',
      password:'',
      code:'',
      smsCode:'',
      wangji:false,
      inviteCode:'',
      codeText:'获取验证码',
				countdown:60,
				isGet:false
    };
  },
  mounted(){
    console.log(this.url)
  },
  methods:{
    getCode(){
      let that=this;
				var myreg = /^(((13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|(17[0-9]{1})|(19[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
				if(!myreg.test(this.account))
				{
					this.$message({
          message: '请填写有效的手机号',
          type: 'warning'
          });
					return false;
				}
				this.settime();
				// this.axios.post(process.env.API_HOST+'user/sms',
				this.axios.post('/giftmall/user/sms',
          this.qs.stringify({
              mobile:this.account
          }),{
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                // 'domain':'taobao1.tianyalei.com',
                'domain':this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            })
        .then(function (response) {
            if(response.data.code==200){
                that.$message({
                message: '验证码已发送成功',
                type: 'success'
                });
            }
          })
			},
			settime(){
				if (this.countdown == 0) {
					this.isGet=false;	
					this.codeText='获取验证码';
					this.countdown = 60;
					return;
				} else {
					this.isGet=true;
					this.codeText=this.countdown+'S';
					this.countdown=this.countdown-1;
				}
				let that=this;
				setTimeout(function() {
					 that.settime() }
				 ,1000)
			},
    login(){
      console.log('url:'+ this.url)
      let that=this;
    //  this.axios.post(process.env.API_HOST+'user/login',
     this.axios.post('/giftmall/user/login',
    //  this.axios.post('47.92.118.219/giftmall/user/login',
     this.qs.stringify({
          account:this.account,
          password:hex_md5(this.password)
      }),{ 
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            // 'domain':'taobao.tianyalei.com',
            'domain':this.url
            // "Authorization":sessionStorage.getItem('token')
          }
        })
    .then(function (response) {
        if(response.data.code==200){
            that.$message({
              message: '登录成功',
              type: 'success'
            });
            sessionStorage.setItem('token',response.data.data)
            that.$router.push('/member')
        }else{
            that.$message({
              message: response.data.message,
              type: 'warning'
            });
        }
       })
    },
    forget(){
      let that=this;
    //  this.axios.post(process.env.API_HOST+'user/forgetPassword',
     this.axios.post('/giftmall/user/forgetPassword',
     this.qs.stringify({
          account:this.account,
          password:hex_md5(this.password),
          smsCode:this.smsCode
      }),{
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            // 'domain':'taobao1.tianyalei.com'
            'domain':this.url
          }
        })
    .then(function (response) {
        if(response.data.code==200){
            that.$message({
              message: '修改成功',
              type: 'success'
            });
            
        }else{
            that.$message({
              message: response.data.message,
              type: 'warning'
            });
        }
       })
    },
    register(){
      this.$router.push('/register')
    }
  }
};
</script>

<style scoped>

.loginCenter {
  background:url(../../static/loginLeft.png) no-repeat 40px center;
  height:533px;
  position:absolute;
  top:50%;
  margin-top:-266px;
  width:1200px;
  left:50%;
  margin-left:-600px;
}
.loginBg {
  background:#fff;
  border-radius: 10px;
  padding:25px;
  float:right;
  margin-top:20px;
  margin-right:20px;
  width:320px;
}
.loginTitle {
  font-size:24px;
}
.loginDesc {
  font-size:15px;
  color:#999;
}
.inp1 {
  margin-top:15px;
  position:relative;
}
.getCode {
  position:absolute;
  right:20px;
  top:0px;
}
.submitBt {
  width:100%;
}

</style>



