<template>
  <div>
    <topMember></topMember>
    <div class="box mar fffbg" style="min-height:500px;">
      <div class="mainH">
        <div class="mainHICO"></div>
        <span class="pull-left">充值</span>
      </div>
      <div class="pad mar">
         <el-form  label-width="80px">
            <el-form-item label="充值金额">
              <el-input type="number" v-model="money" style="width:194px;"></el-input> 元
            </el-form-item>
            <el-form-item label="充值平台">
                  <el-select v-model="type" placeholder="请选择平台">
                    <el-option label="支付宝充值" value="1"></el-option>
                    <el-option label="微信充值" value="2"></el-option>
                  </el-select>
            </el-form-item>
            <el-form-item>
                  <el-button @click="chongzhi" type="primary">确认充值</el-button>
            </el-form-item>
         </el-form>   
        
      </div>
    </div>
    <foot></foot>
    <el-dialog
      title="扫码充值"
      :visible.sync="dialogVisible"
      width="30%">
      <div style="margin:-30px auto 0px auto;width:350px;"><iframe width="350" height="350" style="border:0px;" :src="'http://qr.liantu.com/api.php?w=350&text='+payurl"></iframe></div>
      <div v-if="type=='1'" style="text-align:center">打开支付宝扫码支付</div>
      <div v-if="type=='2'" style="text-align:center">打开微信扫码支付</div>
    </el-dialog>
   
  </div>
</template>

<script>
import foot from "./public/foot.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      money:'',
      payurl:'',
      orderNum:'',
      dialogVisible:false,
      ispay:false,
      type:'1'
    };
  },
  methods: {
    chongzhi(){
      if(this.money==''){
          this.$message({
                message: "请输入金额",
                type: 'warning'
           });
           return;
      }
      let that=this;
      // this.axios.post(process.env.API_HOST+'wallet',
      this.axios.post('/giftmall/wallet',
      this.qs.stringify({
            money:this.money*100,
            type:this.type
        }),{
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
              // 'domain':'taobao1.tianyalei.com',
              'domain':this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          })
      .then(function (response) {
          if(response.data.code==200){
              that.payurl=response.data.data.payinfo;
              that.orderNum=response.data.data.orderNum;
              that.dialogVisible=true;
              that.getState();
          }else{
              that.$message({
                message: response.data.message,
                type: 'warning'
              });
          }
        })
    },
    getState(){
      let that = this;
      setTimeout(function(){ 
            that.axios
              .get(
                // process.env.API_HOST+"wallet/payResult?orderNum="+that.orderNum,
                "/giftmall/wallet/payResult?orderNum="+that.orderNum,
                {
                  headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                    // "domain": "taobao1.tianyalei.com",
                    "domain": this.url,
                    "Authorization":sessionStorage.getItem('token')
                  }
                }
              )
              .then(function(response) {
                if (response.data.code == 200) {
                   if(response.data.data.deleteFlag){
                      that.getState();
                   }else{
                      that.dialogVisible=false;
                      that.$message({
                            message: "支付成功",
                            type: 'success'
                      });
                      that.$router.push('/member')
                  }
                } 
              });
         }, 1000);
    }
  }
};
</script>

<style>

</style>




