        </el-pagination>
       </div>  

                </el-tab-pane>
                <el-tab-pane label="详细订单管理" name="second">
                     <el-row :gutter="15">
                         <el-col :span="5"><el-input v-model="detailForm.id" placeholder="平台订单号"></el-input></el-col>
                         <el-col :span="5"><el-input v-model="detailForm.mobile" placeholder="收件人手机"></el-input></el-col>
                         <el-col :span="5"><el-input v-model="detailForm.orderNum" placeholder="第三方订单号"></el-input></el-col>
                         <el-col :span="4">
                           <el-select placeholder="快递公司">
