            align="center"
            width="100">
            <template slot-scope="scope">
             <el-link type="primary" @click="look()" :underline="false">查看</el-link>
            </template>
          </el-table-column> -->
        </el-table>
       </div>  
       <div class="pageDiv">
         <el-pagination
         background
          @size-change="handleSizeChange2"
          @current-change="handleCurrentChange2"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="size2"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total2">
        </el-pagination>
       </div>  
                      
                      
                      
                </el-tab-pane>
              </el-tabs>
          </div>  
    </div>
    <foot></foot>

    <el-dialog
      title="包裹详情"
      :visible.sync="lookdetail"
      width="40%"
      >
      <div>
       <div class="f20" style="margin-top:-20px;margin-bottom:10px;">收件信息</div>
          收件人姓名：孟维<br/>
          收件人电话：13299992222<br/>
          收件人地址：河北省石家庄市梅花镇梅花村<br/>
          快递状态：已发货<br/>
          <table class="uptable mar" width="100%">
                              <tr>
                                 <th>商品信息</th>
                                 <th>商品单价</th>
                                 <th>数量</th>
                                 <th>单个重量/kg</th>
                              </tr> 
                              <tr>
                                 <td>
                                   <img class="productImg" src="http://baoyitech.oss-cn-hangzhou.aliyuncs.com/15561018380005c74d472e3f16efd762372eaa2298418">
                                   润百洁单包抽纸
                                 </td>
                                 <td align="center">￥0.79</td>
                                 <td align="center">3</td>
                                 <td align="center">0.06</td>
                              </tr> 
                              
                               <tr>
                                 <th colspan="4">包裹重量：0.01kg&nbsp;&nbsp;&nbsp;&nbsp;
费用计算：<strong class="red f20">￥70.55</strong></th>
                              </tr> 
                          </table>  
      </div>
      <div class="mar" style="text-align:center;"><el-button type="primary" @click="lookdetail = false">关闭</el-button></div>
    </el-dialog>

  </div>
</template>

<script>
import foot from './public/foot.vue'
import topMember from './public/topMember.vue'
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      input1: '',
      selected:'1',
      searchInp:'',
      activeName: 'first',
      page:1,
      size:10,
      total:0,
      page2:1,
      size2:10,
      total2:0,
      account:'',
      orderNum:'',
      detailForm:{
        id:'',
        taskId:'',
        mobile:'',
        orderNum:'',
        packNum:'',
        state:'',
        begin:'',
        end:''
      },
      dateValue:'',
      tableData: [],
      tableData2: [],
      lookdetail:false,
      taskIds:''
    };
  },
  mounted(){
    this.taskList();
  },
  methods:{
    taskList(){
        let that = this;
        if(that.selected=='1'){
            that.orderNum=that.searchInp;
            that.account='';
         }else if(that.selected=='2'){
            that.account=that.searchInp;
            that.orderNum=''
         }
        this.axios
          .get(
            // process.env.API_HOST+"task?&page="+(that.page-1)+'&size='+that.size+'&account='+that.account+'&orderNum='+that.orderNum,
            "/giftmall/task?&page="+(that.page-1)+'&size='+that.size+'&account='+that.account+'&orderNum='+that.orderNum,
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                "domain": this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              for(var i=0;i<response.data.data.list.length;i++){
                  if(response.data.data.list[i].payTime){
                      response.data.data.list[i].payTime=that.gettime(response.data.data.list[i].payTime);
                  }
              }
              that.tableData=response.data.data.list;
              that.total=response.data.data.totalCount;
            } 
          });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size=val;
      this.taskList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page=val;
      this.taskList();
    },
    handleSizeChange2(val) {
      console.log(`每页 ${val} 条`);
      this.size2=val;
      this.detailList();
    },
    handleCurrentChange2(val) {
      console.log(`当前页: ${val}`);
      this.page2=val;
      this.detailList();
    },
    gettime(t){
        var _time=new Date(t);
        var   year=_time.getFullYear();//2017
        var   month=_time.getMonth()>8?(_time.getMonth()+1):'0'+(_time.getMonth()+1);//7
        var   date=_time.getDate()>9?_time.getDate():'0'+_time.getDate();//10
        var   hour=_time.getHours()>9?_time.getHours():'0'+_time.getHours();;//10
        var   minute=_time.getMinutes()>9?_time.getMinutes():'0'+_time.getMinutes();;//56
        var   second=_time.getSeconds()>9?_time.getSeconds():'0'+_time.getSeconds();;//15
        
        return   year+"-"+month+"-"+date+"   "+hour+":"+minute+":"+second;//这里自己按自己需要的格式拼接
    },
    detailList(){
        this.detailForm.begin=this.dateValue[0];
        this.detailForm.end=this.dateValue[1];
        this.detailForm.page=this.page2-1;
        this.detailForm.size=this.size2;
      this.activeName='second'
      let that=this;
       this.axios
          .get(
            // process.env.API_HOST+'order?'+that.qs.stringify(that.detailForm),
            '/giftmall/order?'+that.qs.stringify(that.detailForm),
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                "domain": this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              for(var i=0;i<response.data.data.list.length;i++){
                  if(response.data.data.list[i].createTime){
                      response.data.data.list[i].createTime=that.gettime(response.data.data.list[i].createTime);
                  }
              }
              that.tableData2=response.data.data.list;
              that.total2=response.data.data.totalCount;
            } 
          });
    },
      look(){
        this.lookdetail=true;
      },
      plfh(){
        window.open('http://kmd.liangjutong.com/')
      },
      handleSelectionChange(val) {
        console.log(val)
        let arry=[]
        for(var i=0;i<val.length;i++){
            arry.push(val[i].id)
        }
        this.taskIds=arry.join(',');
      },
      daochu(){
        let that=this;
        if(this.taskIds==''){
          that.$message({
              message:"请最少勾选一条数据",
              type: 'warning'
           });
           return
        }
        
        // this.axios.post(process.env.API_HOST+'task/outputExcel',
        this.axios.post('/giftmall/task/outputExcel',
        this.qs.stringify({taskIds:this.taskIds}),{
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'domain':this.url,
                // 'domain':'taobao1.tianyalei.com',

                "Authorization":sessionStorage.getItem('token')
              }
            })
        .then(function (response) {
            if(response.data.code==200){
               let arry=[];
               for (var i=0;i<response.data.data.length;i++){
                  arry.push({
                      订单编号:response.data.data[i].orderId,
                      客户姓名:response.data.data[i].userName,
                      客户地址:response.data.data[i].address,
                      联系电话:response.data.data[i].mobile,
                      快递公司名称:'圆通速递',
                      运单号:response.data.data[i].packNum,
                      邮费总价:response.data.data[i].expressMoney/100,
                      货品总价:response.data.data[i].goodsMoney/100
                  })
               }
               that.JSONToExcelConvertor(arry,'订单列表')
            }else{
                that.$message({
                  message: response.data.message,
                  type: 'warning'
                });
            }
          })
      },
      //json数据转excel
     JSONToExcelConvertor(JSONData, FileName) {  
            //先转化json  
            var arrData = typeof JSONData != 'object' ? JSON.parse(JSONData) : JSONData;  
            var excel = '<table border="1"  rull="all" style="border-collapse: collapse">';   
            var row = "<tr>";           
            //设置表头  
            var keys = Object.keys(JSONData[0]);  
            keys.forEach(function (item) {
                row += "<td>" + item + '</td>';
            });     
            //换行  
            excel += row + "</tr>";  
            //设置数据
            for (var i = 0; i < arrData.length; i++) {  
                var row = "<tr>";  
                for (var index in arrData[i]) {  
                    console.log(arrData[i][index]);
                    //var value = arrData[i][index] === "." ? "" : arrData[i][index];  
                    row += '<td style=mso-number-format:"\@">' + arrData[i][index] + '</td>';  
                }  
                excel += row + "</tr>";  
            }  

            excel += "</table>";  

            var excelFile = "<html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:x='urn:schemas-microsoft-com:office:excel' xmlns='http://www.w3.org/TR/REC-html40'>";  
            excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=UTF-8">';  
            excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel';  
            excelFile += '; charset=UTF-8">';  
            excelFile += "<head>";  
            excelFile += "<!--[if gte mso 9]>";  
            excelFile += "<xml>";  
            excelFile += "<x:ExcelWorkbook>";  
            excelFile += "<x:ExcelWorksheets>";  
            excelFile += "<x:ExcelWorksheet>";  
            excelFile += "<x:Name>";  
            excelFile += "{worksheet}";  
            excelFile += "</x:Name>";  
            excelFile += "<x:WorksheetOptions>";  
            excelFile += "<x:DisplayGridlines/>";  
            excelFile += "</x:WorksheetOptions>";  
            excelFile += "</x:ExcelWorksheet>";  
            excelFile += "</x:ExcelWorksheets>";  
            excelFile += "</x:ExcelWorkbook>";  
            excelFile += "</xml>";  
            excelFile += "<![endif]-->";  
            excelFile += "</head>";  
            excelFile += "<body>";  
            excelFile += excel;  
            excelFile += "</body>";  
            excelFile += "</html>";  

            var uri = 'data:application/vnd.ms-excel;charset=utf-8,' + encodeURIComponent(excelFile);  

            var link = document.createElement("a");      
            link.href = uri;  

            link.style = "visibility:hidden";  
            link.download = FileName + ".xls";  

            document.body.appendChild(link);  
            link.click();  
            document.body.removeChild(link);  
        }
  }
};
</script>

<style>
.title {
  text-align: center;
  font-size:20px;
  padding:20px;
}
.input-with-select .el-select .el-input {
    width: 100px;
  }
.uptable {
  border-collapse: collapse;
}
.uptable td,.uptable th {
  padding:5px;
  font-size:12px;
  border:1px solid #eaeaea;
}
.uptable th {
  background:#f7f7f7;
} 
.productImg {
  float:left;
  margin-right:10px;
  width:50px;
  height:50px;
}

</style>




